import Typewriter from "typewriter-effect";

function Text() {
  return (
    <Typewriter
      onInit={(typewriter) => {
        typewriter
          .typeString("Hey There It's me Shaquib👋")
          .callFunction(() => {})
          .deleteAll(20)
          .pauseFor(200)
          .typeString("How are you?")
          .callFunction(() => {})
          .deleteAll(20)
          .pauseFor(200)
          .typeString("Fine!!! yep thats great")
          .callFunction(() => {})
          .pauseFor(200)
          .deleteAll(20)
          .typeString("{__REPEAT__ AH sheet HerE We gO AgAiN}")
          .callFunction(() => {})
          .pauseFor(200)
          .deleteAll(20)
          .start();
      }}
      options={{
        autoStart: true,
        loop: true,
      }}
    />
  );
}

export default Text;